body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.center {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 0;
  display: flex;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  display: block;
  position: absolute;
  top: calc(50% - 20vmin);
  left: calc(50% - 1.5vmin);
  transform: translate(50%, 100%)rotate(0);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  display: block;
  position: absolute;
  top: calc(50% - 38vmin);
  left: calc(50% - 1.5vmin);
  transform: translate(50%, 100%)rotate(0);
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: block;
  position: absolute;
  top: calc(50% - 5vmin);
  left: calc(50% - 2.5vmin);
  transform: translate(0, 50%);
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
